import React, { Component } from "react";
import { Link } from "react-router-dom";

// Bootstrap UI
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export class Navbar extends Component {
  toggleNavigation(e) {
    e.preventDefault();
    const navbar = document.getElementById('fridayNavbar');
    if(navbar) {
      navbar.classList.toggle('show');
    } else {
      console.log('not set');
    }
  }
  render() {
    return (
      <section className="fr-nav fixed-top bg-white shadow-sm p-3" id="fridayNav">
        <Container>
          <Row id="fridayNavbar" className="fr-navbar justify-content-between align-items-center">
            <Col xs="auto" className="order-0">
              <div onClick={this.toggleNavigation} className="navbar-toggle">
                <span></span>
                <span></span>
                <span></span>
              </div>
            </Col>
            <Col xs={12} md="auto" className="nav-col order-12 order-md-1 mr-md-auto">
              <Link to="/" className="nav-link">about</Link>
              <Link to="/" className="nav-link">work</Link>
              <Link to="/" className="nav-link">trips</Link>
              <Link to="/" className="nav-link">photos</Link>
              <Link to="/" className="nav-link">contact</Link>
            </Col>
            <Col xs="auto" className="order-1 order-md-12">
              <FontAwesomeIcon icon={['far', 'user-visor']} />
            </Col>
          </Row>
        </Container>
      </section>
    )
  }
}

export default Navbar;