import React, { Component, Fragment } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

// Boostrap UI
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

// Components
import Post from "./Post";
import PostSkeleton from "./PostSkeleton";

class PostsList extends Component {
  state = {
    posts: null
  };
  componentDidMount() {
    axios
      .get(`/posts/type/${this.props.type}/status/published?limit=${this.props.limit}`)
      .then(res => {
        this.setState({
          posts: res.data
        });
      })
      .catch(err => console.error(err));
  }
  render() {
    let posts = this.state.posts;
    let recentPostsMarkup = [];
    if ( posts ) {
      for (let i = 0; i < posts.length; i++) {
        if ( i % 3 === 0 && posts.length < 4 ) {
          recentPostsMarkup.push(
            <Col sm={12} md={4} lg={3} key={posts[i].post_id}>
              <Post post={posts[i]} />
            </Col>
          )
        } else if ( i % 6 === 0 && posts.length > 4 && posts.length < 7 ) {
          recentPostsMarkup.push(
            <Col sm={12} md={4} lg={3} key={posts[i].post_id}>
              <Post post={posts[i]} />
            </Col>
          )
        } else if ( i % 3 === 0 && posts.length > 4 ) {
          recentPostsMarkup.push(
            <Col sm={12} md={4} lg={3} key={posts[i].post_id}>
              <Post post={posts[i]} />
            </Col>
          )
        } else if ( i % 2 === 0 ) {
          recentPostsMarkup.push(
            <Col sm={12} md={4} lg={3} key={posts[i].post_id}>
              <Post post={posts[i]} />
            </Col>
          )
        } else {
          recentPostsMarkup.push(
            <Col sm={12} md={4} lg={6} key={posts[i].post_id}>
              <Post post={posts[i]} />
            </Col>
          )
        }
      }
    } else {
      recentPostsMarkup.push(
        <Col sm={12} md={4} lg={3} key="skeleton">
          <PostSkeleton />
        </Col>
      )
    }
    return (
      <Fragment>
        <Container className="py-7">
          <Row>
            <Col><h2 className="text-capitalize mb-5">{this.props.type}s</h2></Col>
          </Row>
          <Row>
            {recentPostsMarkup}
          </Row>
          <Row>
            <Col xs="auto" className="mt-3 ml-auto">
              <Link to={`/posts`} className="btn btn-light">view more {this.props.type}s</Link>
            </Col>
          </Row>
        </Container>
      </Fragment>
    );
  }
}

export default PostsList;
