import React, { Component } from "react";
import { Link } from "react-router-dom";
import dayjs from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";

// Bootstrap UI
import Card from "react-bootstrap/Card";

class Post extends Component {
  render() {
    dayjs.extend(relativeTime);
    const {
      post: {
        post_id,
        excerpt,
        created_date,
        thumbnail,
        title
      }
    } = this.props;
    return (
      <div className="fr-grid  fr-grid-full">
        <Card className="fr-card">
          <Link to={`/post/${post_id}`} className="fr-card-body fr-card-link">
            <span className="fr-card-label">{dayjs(created_date).fromNow()}</span>
            <p className="fr-card-title">{title}</p>
            <span className="fr-card-short text-truncate">{excerpt}</span>
          </Link>
          <div className="fr-card-image" style={{ backgroundImage: `linear-gradient(rgba(5, 10, 35, .0), rgba(5, 10, 35, 1)), url(${thumbnail})` }}></div>
        </Card>
      </div>
    );
  }
}

export default Post;